import {useEffect, useState} from "react";
import {i18nUtil} from "../../../assets/js/common/i18nUtil";
import {commonUtil} from "../../../assets/js/common/commonUtil";
import {GlobalOutline} from "antd-mobile-icons";
import {Modal} from "antd-mobile";

function EnvironmentFeature(props) {
    const [nowTimeInfo, setNowTimeInfo] = useState({});

    function getNowTimeInfo() {
        let nowDate = new Date();
        const dayStrs = i18nUtil.getText('dayStrs');
        return {
            date: nowDate.getFullYear() + '年' + (nowDate.getMonth() + 1) + '月' + nowDate.getDate() + '日',
            chinaDate: commonUtil.getChinaDate(),
            time: nowDate.getHours() + ':' + (nowDate.getMinutes() > 9 ? nowDate.getMinutes() : '0' + nowDate.getMinutes()),
            day: dayStrs[nowDate.getDay()],
        };
    }

    function initData() {
        setNowTimeInfo(getNowTimeInfo());
    }

    function getEnvColor() {
        if (!props.dangerEventList || props.dangerEventList.length === 0) {
            return 'var(--success-color)';
        }
        props.dangerEventList.forEach(dangerEvent => {
            if (/地震|海啸|台风|暴|洪/.test(dangerEvent.title)) {
                return 'var(--danger-color)';
            }
        });
        if (props.dangerEventList.length > 0) {
            return "var(--warn-color)";
        }
    }

    useEffect(function () {
        initData();
        setInterval(() => {
            setNowTimeInfo(getNowTimeInfo());
        }, 1000);
        // eslint-disable-next-line
    }, []);
    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="env-location">{props.locationInfo.city}</div>
                        <div className="env-temp-text">{props.locationInfo.weather}</div>
                    </div>
                    <div className="feature-head-right">
                        <div className="env-date">
                            <div>{nowTimeInfo.date}</div>
                            <div className="flex-center">{nowTimeInfo.chinaDate}</div>
                        </div>
                    </div>
                </div>
                <div className="feature-content">
                    <div className="env-time-info">
                        <div className="env-time">{nowTimeInfo.time}</div>
                        <div className="env-status" onClick={() => {
                            if (!props.dangerEventList || props.dangerEventList.length === 0) {
                                return;
                            }
                            Modal.alert({
                                content:
                                    <>
                                        {
                                            props.dangerEventList.map((dangerEvent, index) => {
                                                return <div style={{fontSize: 'var(--font-size-mid)'}}
                                                            key={index}>{(index + 1) + "." + dangerEvent.title}</div>;
                                            })
                                        }
                                    </>,
                                onConfirm: () => {
                                    console.log('Confirmed')
                                },
                            }).then();
                        }}>
                            <GlobalOutline
                                className={props.dangerEventList && props.dangerEventList.length > 0 ? 'animate-biger-breath' : ''}
                                style={{color: getEnvColor()}}/>
                        </div>
                        <div className="env-day">{nowTimeInfo.day}</div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default EnvironmentFeature;