import {i18nUtil} from "../../../assets/js/common/i18nUtil";
import {CheckCircleFill, RightOutline} from "antd-mobile-icons";
import {storeType, storeUtil} from "../../../assets/js/common/storeUtil";
import {Delete} from "@icon-park/react";
import {commonUtil} from "../../../assets/js/common/commonUtil";

function TodayTodoFeature(props) {
    function getDoneStat() {
        if (props.todayTodoList.length === 0) {
            return '';
        }
        let doneCount = 0;
        for (let todo of props.todayTodoList) {
            if (todo.ok) {
                doneCount += 1;
            }
        }
        return '：' + doneCount + '/' + props.todayTodoList.length;
    }

    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="feature-head-left-title">{i18nUtil.getText('todayTodo') + getDoneStat()}</div>
                    </div>
                    <div className="feature-head-right">
                    </div>
                </div>
                <div className="feature-content">
                    <div className="todayTodo-list"
                         style={{display: props.todayTodoList.length > 0 ? undefined : 'none'}}>
                        {
                            props.todayTodoList.map((todo, index) => {
                                return (
                                    <div className="todayTodo-item" key={index}>
                                        <div className='todayTodo-item-info flex-center'>
                                            <div className='todayTodo-item-title flex-center'>
                                                {todo.title} {new Date(todo.timestamp).toLocaleTimeString()}
                                            </div>
                                            <div
                                                className={todo.ok ? 'todayTodo-item-operate flex-center ok' : 'todayTodo-item-operate flex-center'}
                                                onClick={() => {
                                                    let todayTodoList = [...props.todayTodoList];
                                                    todo.ok = !todo.ok;
                                                    storeUtil.updateById(storeType.TODO_LIST, todo);
                                                    todayTodoList[index] = todo;
                                                    props.setTodayTodoList(todayTodoList);
                                                }}>
                                                <CheckCircleFill/>
                                            </div>
                                            <div
                                                className="todayTodo-item-operate flex-center"
                                                onClick={async () => {
                                                    if (await commonUtil.confirm('确定删除' + todo.title + i18nUtil.getText('todayTodo') + '吗？')) {
                                                        storeUtil.delById(storeType.TODO_LIST, todo.dataId);
                                                        props.loadTodayTodoList();
                                                    }
                                                }}>
                                                <Delete theme="filled" fill='var(--danger-color)' className="del-btn"/>
                                            </div>
                                        </div>
                                        <div
                                            style={{display: index === props.todayTodoList.length - 1 ? 'none' : undefined}}
                                            className='todayTodo-item-icon flex-center'>
                                            <RightOutline/>
                                        </div>
                                    </div>
                                );
                            })
                        }
                    </div>
                    <div style={{display: props.todayTodoList.length === 0 ? undefined : 'none'}}
                         className="sys-empty">{i18nUtil.getText('emptyHit')}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default TodayTodoFeature;