import {CheckCircleFill} from "antd-mobile-icons";
import {storeType, storeUtil} from "../../../assets/js/common/storeUtil";
import {i18nUtil} from "../../../assets/js/common/i18nUtil";
import {Delete} from "@icon-park/react";
import {commonUtil} from "../../../assets/js/common/commonUtil";
import {useEffect} from "react";

function HabitFeature(props) {
    function getDoneStat() {
        if (props.habitList.length === 0) {
            return '';
        }
        let doneCount = 0;
        for (let habit of props.habitList) {
            if (habit.okDates && habit.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
                doneCount += 1;
            }
        }
        return '：' + doneCount + '/' + props.habitList.length;
    }

    function getHabitIcon(habitTile) {
        if (habitTile.indexOf('运动') !== -1) {
            return "🏃";
        } else if (habitTile.indexOf('水') !== -1) {
            return "🥤";
        } else if (habitTile.indexOf('起床') !== -1) {
            return "🛏️";
        } else if (habitTile.indexOf('睡') !== -1) {
            return "💤";
        } else if (habitTile.indexOf('蔬果') !== -1) {
            return "🍎";
        } else if (habitTile.indexOf('学习') !== -1) {
            return "📖";
        } else if (habitTile.indexOf('拓展') !== -1) {
            return "📚";
        } else if (habitTile.indexOf('兴趣') !== -1) {
            return "🎮";
        } else if (habitTile.indexOf('项目') !== -1) {
            return "🚀";
        } else if (habitTile.indexOf('实践') !== -1) {
            return "🛠️";
        } else if (habitTile.indexOf('习惯') !== -1) {
            return "⭐️";
        } else if (habitTile.indexOf('吃') !== -1 || habitTile.indexOf('饮食') !== -1) {
            return "🍽️️";
        } else {
            return "🔁"
        }
    }

    function getHabitTotalOkDays() {
        const habitOkDays = storeUtil.getItem(storeType.HABIT_OK_DAYS);
        return habitOkDays ? habitOkDays : 0;
    }

    function handleHabitOk(habit, index) {
        let habitListTemp = [...props.habitList];
        if (!habit.okDates) {
            habit.okDates = [];
        }
        if (habit.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
            habit.okDates = habit.okDates.filter(date => date !== new Date().toLocaleDateString());
        } else {
            habit.okDates.push(new Date().toLocaleDateString());
        }
        habitListTemp[index] = habit;
        props.setHabitList(habitListTemp);
        storeUtil.updateById(storeType.TODO_LIST, habit);
        // 判断是否全部完成
        let doneCount = 0;
        for (let habit of props.habitList) {
            if (habit.okDates && habit.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
                doneCount += 1;
            }
        }
        const habitOkDays = storeUtil.getItem(storeType.HABIT_OK_DAYS);
        if (doneCount === props.habitList.length) {
            storeUtil.saveItem(storeType.HABIT_OK_DAYS, habitOkDays ? habitOkDays + 1 : 1);
        } else {
            if (habitOkDays) {
                storeUtil.saveItem(storeType.HABIT_OK_DAYS, habitOkDays - 1);
            }
        }
    }

    useEffect(() => {

    }, []);

    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="feature-head-left-title">{i18nUtil.getText('habit') + getDoneStat()}</div>
                    </div>
                    <div className="feature-head-right">
                        <div style={{display: props.habitList.length > 0 ? undefined : 'none'}}>
                            <span>已达标</span>
                            <span className="feature-head-right-btn"> {getHabitTotalOkDays()} </span>
                            <span>天</span>
                        </div>
                    </div>
                </div>
                <div className="feature-content">
                    <div className="habit-list">
                        {
                            props.habitList.map((habit, index) => {
                                return (
                                    <div className="habit-item" key={index}>
                                        <div className="habit-item-icon flex-center"
                                             onClick={() => handleHabitOk(habit, index)}>
                                            {getHabitIcon(habit.title)}
                                            <span
                                                style={{display: (habit.okDates && habit.okDates.indexOf(new Date().toLocaleDateString()) !== -1) ? undefined : 'none'}}
                                                className="habit-item-ok"><CheckCircleFill/></span>
                                        </div>
                                        <div>
                                            <div
                                                className="habit-item-time flex-center">{habit.date.hour + ":" + habit.date.minute}</div>
                                            <div className="habit-item-title flex-center">{habit.title}</div>
                                            <div className="habit-item-stat flex-center">
                                                已坚持&nbsp;
                                                <span style={{color: 'var(--warn-color)'}}>
                                                    {habit.okDates ? habit.okDates.length : 0}
                                                </span>
                                                &nbsp;天
                                            </div>
                                            <div className="habit-item-operate">
                                                <Delete theme="filled" fill='var(--danger-color)' className="del-btn"
                                                        onClick={async () => {
                                                            if (await commonUtil.confirm('确定删除' + habit.title + i18nUtil.getText('habit') + '吗？')) {
                                                                storeUtil.delById(storeType.TODO_LIST, habit.dataId);
                                                                props.loadHabitList();
                                                            }
                                                        }}/>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })
                        }
                    </div>
                    <div style={{display: props.habitList.length === 0 ? undefined : 'none'}}
                         className="sys-empty">{i18nUtil.getText('emptyHit')}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default HabitFeature;