import EnvironmentFeature from "./featureItem/EnvironmentFeature";
import EventFeature from "./featureItem/EventFeature";
import HabitFeature from "./featureItem/HabitFeature";
import TimerFeature from "./featureItem/TimerFeature";
import TodayTodoFeature from "./featureItem/TodayTodoFeature";
import NoteFeature from "./featureItem/NoteFeature";
import "../../assets/css/page/feature.css";
import WalletFeature from "./featureItem/WalletFeature";

function FeatureList(props) {
    return (
        <div className="feature-list" style={{display: props.show ? undefined : 'none'}}>
            <EnvironmentFeature locationInfo={props.locationInfo} dangerEventList={props.dangerEventList}/>
            <WalletFeature
                monthMaybeOut={props.monthMaybeOut}
                monthOutMoney={props.monthOutMoney}
                monthMaybeStore={props.monthMaybeStore}
                yearMaybeStore={props.yearMaybeStore}
                totalOutMoney={props.totalOutMoney}
                totalMoney={props.totalMoney}
                walletRecord={props.walletRecord}
                loadWalletData={() => props.loadWalletData()}/>
            <TodayTodoFeature todayTodoList={props.todayTodoList} setTodayTodoList={(value) => {
                props.setTodayTodoList(value);
            }} loadTodayTodoList={() => props.loadTodayTodoList()}/>
            <HabitFeature habitList={props.habitList} setHabitList={(value) => {
                props.setHabitList(value);
            }} loadHabitList={() => props.loadHabitList()}/>
            <EventFeature eventList={props.eventList} loadEventList={() => {
                props.loadEventList()
            }}/>
            <NoteFeature noteList={props.noteList} setNoteList={(value) => {
                props.setNoteList(value);
            }} loadNoteList={() => props.loadNoteList()}/>
            <TimerFeature timerList={props.timerList} setTimerList={(value) => {
                props.setTimerList(value);
            }} loadTimerList={() => props.loadTimerList()}/>
        </div>
    );
}

export default FeatureList;