import {i18nUtil} from "../../../assets/js/common/i18nUtil";
import TimeCountDown from "../../../component/TimeCountDown";
import {Delete} from "@icon-park/react";
import {storeType, storeUtil} from "../../../assets/js/common/storeUtil";
import {commonUtil} from "../../../assets/js/common/commonUtil";

function TimerFeature(props) {
    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="feature-head-left-title">{i18nUtil.getText('timer')}</div>
                    </div>
                    <div className="feature-head-right">
                    </div>
                </div>
                <div className="feature-content">
                    <div className="timer-list" style={{display: props.timerList.length > 0 ? undefined : 'none'}}>
                        {
                            props.timerList.map((timer, index) => {
                                return (
                                    <div className='operate-row' key={index}>
                                        <div className='timer-list-item'>
                                            <div className='timer-list-item-title flex-center'>
                                                {timer.title === '' ? i18nUtil.getText('timer') : timer.title}
                                            </div>
                                            <div className='timer-list-item-time flex-center'>
                                                <TimeCountDown timestamp={timer.timestamp} title={timer.title}/>
                                            </div>
                                        </div>
                                        <div className="operate-row-btn">
                                            <Delete theme="filled" fill='var(--danger-color)' className="del-btn"
                                                    onClick={async () => {
                                                        if (await commonUtil.confirm('确定删除' + timer.title + i18nUtil.getText('timer') + '吗？')) {
                                                            storeUtil.delById(storeType.TODO_LIST, timer.dataId);
                                                            props.loadTimerList();
                                                        }
                                                    }}/>
                                        </div>
                                    </div>
                                );
                            })
                        }
                    </div>
                    <div style={{display: props.timerList.length === 0 ? undefined : 'none'}}
                         className="sys-empty">{i18nUtil.getText('emptyHit')}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default TimerFeature;