import {commonUtil} from "./commonUtil";

export const storeType = {
    CHAT_LIST: 'chatList',
    TODO_LIST: 'todoList',
    TODO_OK: 'todoOk',
    EVENT_LIST: ' eventList',
    GPT_API_KEY: 'gptApiKey',
    ASSISTANT_LOGO: 'assistantLogo',
    LOCATION: 'location',
    HABIT_OK_DAYS: 'habitOkDays',
    NOTE_LIST: 'noteList',
    WALLET_RECORD: 'walletRecord',
    WALLET_MONTH_MAYBE_OUT: 'walletMonthMaybeOut',
}
export const storeUtil = {
    saveItem(k, v) {
        if (!v) {
            localStorage.removeItem(k);
        }
        localStorage.setItem(k, JSON.stringify(v));
    },
    getItem(k) {
        return localStorage.getItem(k) ? JSON.parse(localStorage.getItem(k)) : undefined;
    },
    delItem(k) {
        return localStorage.removeItem(k);
    },
    save(dataType, data) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        data.dataId = new Date().getTime();
        data.createTime = new Date().getTime();
        dataList.push(data);
        localStorage.setItem(dataType, JSON.stringify(dataList));
        return data.dataId;
    },
    delById(dataType, dataId) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        dataList = dataList.filter(data => data.dataId !== dataId);
        localStorage.setItem(dataType, JSON.stringify(dataList));
        return true;
    },
    delAll(dataType) {
        localStorage.removeItem(dataType);
    },
    saveAll(dataType, data) {
        localStorage.setItem(dataType, JSON.stringify(data));
    },
    updateById(dataType, newData) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        dataList = dataList.filter(data => data.dataId !== newData.dataId);
        newData.updateTime = new Date().getTime();
        dataList.push(newData);
        localStorage.setItem(dataType, JSON.stringify(dataList));
        return true;
    },
    getDataList(dataType) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        let dataList = JSON.parse(dataListStr);
        dataList = commonUtil.sortArray(dataList, 'createTime', false);
        return dataList;
    },
    getById(dataType, dataId) {
        let dataListStr = localStorage.getItem(dataType);
        if (!dataListStr) {
            dataListStr = '[]';
        }
        const dataList = JSON.parse(dataListStr);
        return dataList.filter(item => item.dataId === dataId)[0];
    },
    storeLocation() {
        if ('geolocation' in navigator) {
            navigator.geolocation.getCurrentPosition(
                (position) => {
                    const {latitude, longitude} = position.coords;
                    storeUtil.saveItem(storeType.LOCATION, {x: longitude, y: latitude});
                },
                (error) => {
                    console.error('获取位置信息出错:', error);
                }
            );
        } else {
            console.error('浏览器不支持地理定位');
        }
    },
}
