import {CheckCircleFill, CloseCircleFill, EyeFill} from "antd-mobile-icons";
import ReactMarkdown from "react-markdown";
import SyntaxHighlighter from "react-syntax-highlighter/dist/cjs/default-highlight";
import {dark} from "react-syntax-highlighter/dist/cjs/styles/hljs";
import {i18nUtil} from "../assets/js/common/i18nUtil";
import {useState} from "react";
import {storeType, storeUtil} from "../assets/js/common/storeUtil";
import {Toast} from "antd-mobile";

function MarkdownViewModal(props) {
    const [editNote, setEditNote] = useState({title: '', content: ''});
    const [preview, setPreview] = useState(false);

    function hide() {
        props.hide();
        let newEditNote = {...editNote};
        newEditNote.title = '';
        newEditNote.content = '';
        setEditNote(newEditNote)
        setPreview(false);
    }

    return (
        <div>
            <div className="modal-mask" style={{display: props.show ? undefined : "none"}}></div>
            <div className="markdown-view animate-scale-in" style={{display: props.show ? undefined : "none"}}>
                <div className="markdown-view-title">
                    <span style={{display: !props.edit ? undefined : "none"}}>
                        {props.note.title}
                    </span>
                    <span style={{display: props.edit ? undefined : "none"}}>
                        <input placeholder={i18nUtil.getText('title')} value={editNote.title} onChange={(e) => {
                            setEditNote({...editNote, title: e.target.value});
                        }}/>
                    </span>
                </div>
                <div className="markdown-view-time-type">
                    <span style={{display: !props.edit ? undefined : "none"}}>
                        {props.note.time}
                    </span>
                    <span style={{display: props.edit ? undefined : "none"}}>
                        {new Date().toLocaleDateString()}
                    </span>
                </div>
                <div className="markdown-view-content" style={{display: !props.edit || preview ? undefined : "none"}}>
                    <ReactMarkdown
                        children={props.note.content || editNote.content}
                        components={{
                            code: ({language, children}) => (
                                <SyntaxHighlighter language={language} style={dark}>
                                    {children}
                                </SyntaxHighlighter>
                            )
                        }}/>
                </div>
                <div className="markdown-view-content" style={{display: props.edit && !preview ? undefined : "none"}}>
                    <textarea placeholder={i18nUtil.getText('content')}
                              value={editNote.content}
                              onChange={(e) => {
                                  setEditNote({...editNote, content: e.target.value});
                              }}></textarea>
                </div>
                <div className="markdown-view-operate">
                    <EyeFill
                        style={{
                            display: props.edit ? undefined : "none",
                            color: preview ? 'var(--primary-color)' : 'var(--main-text-color-2)'
                        }}
                        className="markdown-view-operate-btn"
                        onClick={() => {
                            if (editNote.content) {
                                setPreview(!preview);
                            }
                        }}/>
                    <CheckCircleFill style={{display: props.edit ? undefined : "none", color: 'var(--success-color)'}}
                                     className="markdown-view-operate-btn"
                                     onClick={() => {
                                         if (!editNote.title) {
                                             Toast.show('标题为空');
                                             return;
                                         }
                                         if (!editNote.content) {
                                             Toast.show('内容为空');
                                             return;
                                         }
                                         editNote.time = new Date().toLocaleDateString();
                                         storeUtil.save(storeType.NOTE_LIST, editNote);
                                         props.loadNoteList();
                                         hide();

                                     }}/>
                    <CloseCircleFill className="markdown-view-operate-btn" style={{color: 'var(--danger-color)'}}
                                     onClick={() => {
                                         let newEditNote = {...editNote};
                                         newEditNote.title = '';
                                         newEditNote.content = '';
                                         setEditNote(newEditNote)
                                         hide();
                                     }}/>
                </div>
            </div>
        </div>
    )
}

export default MarkdownViewModal;