import {useState} from "react";
import {i18nUtil} from "../../assets/js/common/i18nUtil";
import {ClockCircleFill} from "antd-mobile-icons";
import AskRecord from "./AskRecord";
import {ArrowCircleUp} from "@icon-park/react";
import "../../assets/css/page/chat.css";

function AskInput(props) {
    const [sendText, setSendText] = useState('');
    return (
        <div className="ask-input animate-popUp" style={{display: props.show ? undefined : 'none'}}>
            <AskRecord show={props.showChatRecords} chatRecords={props.chatRecords}
                       loadChatRecords={() => props.loadChatRecords()}
                       showChatTalk={(record) => props.showChatTalk(record)}/>
            <div className="ask-send-input flex-center">
                <div
                    className={props.chatRecords.length > 0 ? "ask-left-btn flex-center" : "ask-left-btn ask-left-btn-empty flex-center"}
                    onClick={() => {
                        if (props.showChatRecords) {
                            props.setShowChatRecords(false);
                        } else {
                            props.setShowChatRecords(true);
                        }
                    }}>
                    <ClockCircleFill/>
                </div>
                <input type="text" value={sendText} onInput={(e) => {
                    setSendText(e.target.value);
                }} placeholder={i18nUtil.getText('talkMe') + '...'}/>
                <div className={sendText === '' ? "ask-right-btn flex-center" : "ask-right-btn ok flex-center"}
                     onClick={() => {
                         props.handleSendText(sendText);
                         setSendText('');
                     }}>
                    <ArrowCircleUp className="flex-center"
                                   theme="filled"
                                   fill={sendText === '' ? "var(--main-text-color-2)" : "var(--primary-color)"}/>
                </div>
            </div>
        </div>
    );
}

export default AskInput;