import {storeType, storeUtil} from './storeUtil';
import {i18nUtil} from './i18nUtil';
import {Dialog} from "antd-mobile";

const lunisolar = require('lunisolar');

export const commonConstant = {
    WINDOWS: 'Windows',
    MAC: 'Mac',
    IOS: 'ios',
    ANDROID: 'Android',
}
export const commonUtil = {
    formatTimeNumber(timeNumber) {
        return parseInt(timeNumber) > 9 ? timeNumber : "0" + timeNumber;
    },
    getDeviceType() {
        var userAgent = navigator.userAgent;
        if (/iPhone|iPad|iPod/.test(userAgent)) {
            return commonConstant.IOS;
        } else if (/Android/.test(userAgent)) {
            return commonConstant.ANDROID;
        } else if (/Macintosh/.test(userAgent) && /Intel Mac OS X/.test(userAgent)) {
            return commonConstant.MAC;
        } else if (/Windows/.test(userAgent)) {
            return commonConstant.WINDOWS;
        } else {
            return commonConstant.MAC;
        }
    },
    scrollBodyToBottom() {
        const element = document.documentElement || document.body;
        if (element) {
            element.scrollTop = element.scrollHeight + 80 + 90;
        }
    },
    scrollBodyToTop() {
        const element = document.documentElement || document.body;
        if (element) {
            element.scrollTop = 0;
        }
    },
    scrollToTop(id) {
        const element = document.getElementById(id);
        if (element) {
            element.scrollTop = 0;
        }
    },
    scrollToBottom(id) {
        const element = document.getElementById(id);
        if (element) {
            element.scrollTop = element.scrollHeight;
        }
    },
    scrollToLeft(id) {
        const element = document.getElementById(id);
        if (element) {
            element.scrollLeft = 0;
        }
    },
    sortArray(array, sortKey, asc) {
        if (asc == null) {
            asc = true;
        }
        if (!array || array.length === 0) {
            return array;
        }
        for (let i = 0; i < array.length - 1; i++) {
            for (let j = 0; j < array.length - i - 1; j++) {
                let temp;
                if (asc) {
                    if (array[j + 1][sortKey] < array[j][sortKey]) {
                        temp = array[j];
                        array[j] = array[j + 1];
                        array[j + 1] = temp;
                    }
                } else {
                    if (array[j + 1][sortKey] > array[j][sortKey]) {
                        temp = array[j];
                        array[j] = array[j + 1];
                        array[j + 1] = temp;
                    }
                }
            }
        }
        return array;
    },
    getCssVariable(variableName) {
        const root = document.documentElement;
        const style = getComputedStyle(root);
        return style.getPropertyValue(variableName).trim();
    },
    downloadJsonFile(jsonData, fileName) {
        const jsonString = JSON.stringify(jsonData, null, 2);
        const blob = new Blob([jsonString], {type: 'application/json'});
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = fileName;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
    },
    getChinaDate() {
        // const chinaDateObj = lunisolar('2022/07/18 14:40')
        const chinaDateObj = lunisolar(new Date().toLocaleDateString());
        let chinaMonthName = chinaDateObj.lunar.getMonthName();
        let chinaDayName = chinaDateObj.lunar.getDayName();
        switch (chinaMonthName) {
            case '一月':
                chinaMonthName = '正月'
                break;
            case '十一月':
                chinaMonthName = '冬月'
                break;
            case '十二月':
                chinaMonthName = '腊月'
                break;
            default:
                break;
        }
        return chinaMonthName + chinaDayName;
    },
    getLeftTimeStr(timestamp) {
        let timeObj = new Date(timestamp);
        let leftMils = timeObj.getTime() - new Date().getTime();
        let leftDay = Math.floor(leftMils / 1000 / 60 / 60 / 24);
        let leftHour = Math.floor(leftMils / 1000 / 60 / 60 % 24);
        let leftMinute = Math.floor(leftMils / 1000 / 60 % 60);
        let leftSecond = Math.floor((leftMils % 60000) / 1000);
        if (leftDay > 0) {
            if (leftHour > 0 || leftMinute > 0 || leftSecond > 0) {
                leftDay += 1;
            }
            return leftDay + " " + i18nUtil.getText('day');
        } else if (leftHour > 0) {
            return leftHour + " " + i18nUtil.getText('hour');
        } else if (leftMinute > 0 || leftSecond > 0) {
            return commonUtil.formatTimeNumber(leftMinute) + ":" + commonUtil.formatTimeNumber(leftSecond);
        }
        if (leftDay === 0 || leftHour === 0 || leftMinute === 0 || leftSecond <= 0) {
            return 0;
        }
    },
    getDateFromChinaDate(year, month, date) {
        return lunisolar.fromLunar({year: year, month: month, day: date}).format('YYYY-MM-DD');
    },
    getHolidayDate() {
        const now = new Date();
        const storeHolidays = storeUtil.getItem(now.toDateString + "holidays");
        if (storeHolidays) {
            return storeHolidays;
        }
        // const preChinaNewYear = this.getDateFromChinaDate(now.getFullYear() - 1, 12, 29);
        const chinaNewYear = this.getDateFromChinaDate(now.getFullYear(), 1, 1);
        const yuanXiao = this.getDateFromChinaDate(now.getFullYear(), 1, 15);
        const zhongQiu = this.getDateFromChinaDate(now.getFullYear(), 8, 15);
        const holidays = {
            // "除夕夜": new Date(now.getFullYear(), preChinaNewYear.split("-")[1] - 1, preChinaNewYear.split("-")[2]).getTime(),
            "新年": new Date(now.getFullYear() + 1, 1 - 1, 1).getTime(),
            "春节": new Date(chinaNewYear.split("-")[0], chinaNewYear.split("-")[1] - 1, chinaNewYear.split("-")[2]).getTime(),
            "元宵节": new Date(yuanXiao.split("-")[0], yuanXiao.split("-")[1] - 1, yuanXiao.split("-")[2]).getTime(),
            "劳动节": new Date(now.getFullYear(), 5 - 1, 1).getTime(),
            "中秋节": new Date(zhongQiu.split("-")[0], zhongQiu.split("-")[1] - 1, zhongQiu.split("-")[2]).getTime(),
            "国庆节": new Date(now.getFullYear(), 10 - 1, 1).getTime(),
            "圣诞节": new Date(now.getFullYear(), 12 - 1, 25).getTime(),
        };
        storeUtil.saveItem(now.getFullYear() + "-holidays", holidays);
        return holidays;
    },
    getHelloText() {
        const hour = new Date().getHours();
        if (hour < 5) {
            return '夜深了';
        } else if (hour < 7) {
            return '凌晨了';
        } else if (hour < 9) {
            return '早上好';
        } else if (hour < 12) {
            return '上午好';
        } else if (hour < 14) {
            return '中午好';
        } else if (hour < 18) {
            return '下午好';
        } else if (hour < 20) {
            return '晚上好';
        } else if (hour < 24) {
            return '夜深了';
        }
    },
    getIndexHelloText() {
        let helloText = this.getHelloText();
        let helloExt = '有什么可以帮您？';
        if (helloText === '夜深了') {
            helloExt = '早点休息吧！';
        }
        if (helloText === '早上好') {
            helloExt = '新的一天开始了！';
        }
        return helloText + "，" + helloExt;
    },

    async getWeatherInfo() {
        const location = storeUtil.getItem(storeType.LOCATION);
        if (!location) {
            return '--℃';
        }
        return fetch('https://devapi.qweather.com/v7/weather/now?location=' + location.x + "," + location.y, {
            method: 'GET',
            headers: {
                'X-QW-Api-Key': 'ec9fbdb548e14bbe98938c5557874111'
            },
        }).then(res => res.json()).then(data => {
            let nowWeather = data.now;
            let nowWeatherText = data.now.text;
            let nowWeatherIcon = this.getWeatherIcon(nowWeatherText);
            let nowWeatherTemp = nowWeather.temp + '℃';
            return nowWeatherIcon + " " + nowWeatherTemp;
        }).catch((error) => {
            console.log(error);
            return "--℃"
        });
    },

    getWeatherIcon(weatherText) {
        const weatherIcons = {
            '晴': '☀️',
            '阴': '☁️',
            '雨': '🌧️',
            '雪': '❄️',
            '雷': '⛈️',
            '雾': '🌫️',
            '霾': '🌫️',
            '风': '💨',
            '云': '☁️',
        };

        // 查找匹配的图标
        for (const [key, icon] of Object.entries(weatherIcons)) {
            if (weatherText.includes(key)) {
                return icon;
            }
        }
        return weatherText; // 默认图标
    },

    async getLocationInfo() {
        const location = storeUtil.getItem(storeType.LOCATION);
        if (!location) {
            return i18nUtil.getText('unknownLocation');
        }
        return fetch('https://geoapi.qweather.com/v2/city/lookup?location=' + location.x + "," + location.y, {
            method: 'GET',
            headers: {
                'X-QW-Api-Key': 'ec9fbdb548e14bbe98938c5557874111'
            },
        }).then(res => res.json()).then(data => {
            return data.location[0].adm2 + "·" + data.location[0].name;
        }).catch((error) => {
            console.log(error);
            return i18nUtil.getText('unknownLocation');
        });
    },
    async getDangerEvents() {
        let lang = navigator.language ? navigator.language.split("-")[0] : 'en';
        const location = storeUtil.getItem(storeType.LOCATION);
        if (!location) {
            return [];
        }
        return fetch('https://devapi.qweather.com/v7/warning/now?lang=' + lang + '&location=' + location.x + "," + location.y, {
            method: 'GET',
            headers: {
                'X-QW-Api-Key': 'ec9fbdb548e14bbe98938c5557874111'
            },
        }).then(res => res.json()).then(data => {
            return data.warning;
        }).catch((error) => {
            console.log(error);
            return [];
        });
    },
    async confirm(content) {
        return await Dialog.confirm({
            content: content,
        });
    }
}