import ReactMarkdown from "react-markdown";
import SyntaxHighlighter from "react-syntax-highlighter/dist/cjs/default-highlight";
import {dark} from "react-syntax-highlighter/dist/cjs/styles/hljs";
import "../../assets/css/page/chat.css";

function AnswerModal(props) {
    return (
        <div>
            <div className="modal-mask"
                 style={{display: props.content.ask === '' ? 'none' : undefined}}
                 onClick={() => props.close()}></div>
            <div className="assistant-answer animate-scale-in"
                 onClick={(e) => {
                     e.stopPropagation();
                 }}
                 style={{display: props.content.ask === '' ? 'none' : undefined}}>
                <div className="assistant-answer-title">{props.content.ask}</div>
                <div id="assistant-answer-content" className="assistant-answer-content">
                    <div style={{display: props.content.answerType === 'md' ? undefined : 'none'}}>
                        <ReactMarkdown
                            children={props.content.answer}
                            components={{
                                code: ({language, children}) => (
                                    <SyntaxHighlighter language={language} style={dark}>
                                        {children}
                                    </SyntaxHighlighter>
                                )
                            }}/>
                    </div>
                    <div style={{display: props.content.answerType === 'text' ? undefined : 'none'}}>
                        {props.content.answer}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AnswerModal;