import {storeType, storeUtil} from "../../assets/js/common/storeUtil";
import {commonUtil} from "../../assets/js/common/commonUtil";
import {Delete} from "@icon-park/react";
import "../../assets/css/page/chat.css";

function AskRecord(props) {
    return (
        <div className="chat-record animate-popUp" style={{display: props.show ? undefined : 'none'}}>
            <div id="chat-record-list" className="chat-record-list">
                {
                    props.chatRecords.map((record, index) => {
                        return (
                            <div key={index} className="chat-record-list-item flex-center">
                                <div className="chat-record-list-content flex-center" onClick={() => {
                                    props.showChatTalk(record);
                                }}>
                                    {record.content.length > 10 ? record.content.substring(0, 10) + '...' : record.content}
                                </div>
                                <Delete theme="filled"
                                        fill='var(--danger-color)' style={{marginLeft: '10px'}}
                                        className="del-btn" onClick={async () => {
                                    if (await commonUtil.confirm('确定删除' + record.content + '吗？')) {
                                        storeUtil.delById(storeType.CHAT_LIST, record.dataId);
                                        props.loadChatRecords();
                                    }
                                }}/>
                            </div>
                        );
                    })
                }
            </div>
        </div>
    );
}

export default AskRecord;