import {i18nUtil} from "../common/i18nUtil";
import {commonUtil} from "../common/commonUtil";
import {storeType, storeUtil} from "../common/storeUtil";

const localAskHandleStrategy = {
    "你好|你有什么功能|帮助": {
        analysis: (userMsg) => {
            return userMsg;
        },
        handle: (analysisRes) => {
            return i18nUtil.getText('helloText');
        },
    },
    "(.*?)(提醒我)(.*)": {
        analysis: (userMsg) => {
            let res = {};
            if (!userMsg) {
                return undefined;
            }
            // 1.获取时间和标题
            const remidRegex = new RegExp(/^(.*?)(提醒我)(.*)$/);
            const matchResult = userMsg.match(remidRegex);
            const leftTextStr = matchResult[1].trim();
            const rightTextStr = matchResult[3].trim();
            res.title = rightTextStr;
            // 2.计算时间毫秒值
            const nowDate = new Date();
            let nowDay = nowDate.getDay();
            if (nowDay === 0) {
                nowDay = 7;
            }
            const hourTimerRegex = new RegExp(/^(\d+)(小时后)$/);
            const minuteTimerRegex = new RegExp(/^(\d+)(分后)$/);
            const secondTimerRegex = new RegExp(/^(\d+)(秒后)$/);
            const dayTodoRegex = new RegExp(/^(\d{1,2})月(\d{1,2})日(\d{1,2})点(\d{0,2})/);
            const dateTimeTodoRegex = new RegExp(/^(\d{4}年|今年|每年)(\d{1,2})月(\d{1,2})日(\d{1,2})点(\d{0,2})/);
            const timeTodoRegex = new RegExp(/^(今天|明天|每天|工作日|休息日|每周六|每周日)(\d{1,2})点(\d{0,2})/);
            const hourTimerRegexMatch = leftTextStr.match(hourTimerRegex);
            const minuteTimerRegexMatch = leftTextStr.match(minuteTimerRegex);
            const secondTimerRegexMatch = leftTextStr.match(secondTimerRegex);
            const dayTodoRegexMatch = leftTextStr.match(dayTodoRegex);
            const dateTimeTodoRegexMatch = leftTextStr.match(dateTimeTodoRegex);
            const timeTodoRegexMatch = leftTextStr.match(timeTodoRegex);
            if (hourTimerRegexMatch && hourTimerRegexMatch.length > 0) {
                const hourValue = parseInt(hourTimerRegexMatch[1]);
                let todoDate = new Date(nowDate.getTime() + hourValue * 60 * 60 * 1000);
                res.timestamp = todoDate.getTime();
                res.timeDesc = todoDate.toLocaleString();
                res.type = 'timer';
            } else if (minuteTimerRegexMatch && minuteTimerRegexMatch.length > 0) {
                const minuteValue = minuteTimerRegexMatch[1];
                let todoDate = new Date(nowDate.getTime() + minuteValue * 60 * 1000);
                res.timestamp = todoDate.getTime();
                res.timeDesc = todoDate.toLocaleString();
                res.type = 'timer';
            } else if (secondTimerRegexMatch && secondTimerRegexMatch.length > 0) {
                const secondValue = secondTimerRegexMatch[1];
                let todoDate = new Date(nowDate.getTime() + secondValue * 1000);
                res.timestamp = todoDate.getTime();
                res.timeDesc = todoDate.toLocaleString();
                res.type = 'timer';
            } else if (dateTimeTodoRegexMatch && dateTimeTodoRegexMatch.length > 0) {
                let yearValue = parseInt(dateTimeTodoRegexMatch[1]);
                if (dateTimeTodoRegexMatch[1] === '今') {
                    yearValue = new Date().getFullYear();
                }
                if (dateTimeTodoRegexMatch[1] === '每') {
                    yearValue = '每';
                }
                const monthValue = parseInt(dateTimeTodoRegexMatch[2]);
                const dateValue = parseInt(dateTimeTodoRegexMatch[3]);
                const hourValue = parseInt(dateTimeTodoRegexMatch[4]);
                let minuteValue = parseInt(dateTimeTodoRegexMatch[5]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                res.date = {
                    month: commonUtil.formatTimeNumber(monthValue),
                    date: commonUtil.formatTimeNumber(dateValue),
                    hour: commonUtil.formatTimeNumber(hourValue),
                    minute: commonUtil.formatTimeNumber(minuteValue),
                };
                if (yearValue === '每') {
                    res.repeate = true;
                    res.timeDesc = "每年" + res.date.month + "月" + res.date.date + "日 " + res.date.hour + ":" + res.date.minute;
                    res.type = 'repeatTodo';
                } else {
                    const todoDate = new Date(yearValue, monthValue - 1, dateValue, hourValue, minuteValue, 0)
                    res.timestamp = todoDate.getTime();
                    res.timeDesc = todoDate.toLocaleString();
                    res.type = 'onceTodo';
                }
            } else if (timeTodoRegexMatch && timeTodoRegexMatch.length > 0) {
                const todoHour = parseInt(timeTodoRegexMatch[2]);
                let todoMinute = parseInt(timeTodoRegexMatch[3]);
                if (!todoMinute || todoMinute === '') {
                    todoMinute = 0;
                }
                let todoHourStr = commonUtil.formatTimeNumber(todoHour);
                let todoMinuteStr = commonUtil.formatTimeNumber(todoMinute);
                switch (timeTodoRegexMatch[1]) {
                    case '今天':
                        res.type = 'onceTodo';
                        res.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), todoHour, todoMinute, 0).getTime();
                        res.timeDesc = "今天" + todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '明天':
                        res.type = 'onceTodo';
                        const tomorrowDate = new Date(nowDate.getTime() + 24 * 60 * 60 * 1000);
                        res.timestamp = new Date(tomorrowDate.getFullYear(), tomorrowDate.getMonth(), tomorrowDate.getDate(), todoHour, todoMinute, 0).getTime();
                        res.timeDesc = "明天" + todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '每天':
                        res.type = 'repeatTodo';
                        res.repeatType = 'everyday';
                        res.timeDesc = "每天" + todoHourStr + ":" + todoMinuteStr;
                        res.date = {
                            hour: todoHourStr,
                            minute: todoMinuteStr,
                        };
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '工作日':
                        res.type = 'repeatTodo';
                        res.repeatType = 'workday';
                        res.timeDesc = "工作日" + todoHourStr + ":" + todoMinuteStr;
                        res.date = {
                            hour: todoHourStr,
                            minute: todoMinuteStr,
                        };
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '休息日':
                        res.type = 'repeatTodo';
                        res.repeatType = 'restday';
                        res.timeDesc = "休息日" + todoHourStr + ":" + todoMinuteStr;
                        res.date = {
                            hour: todoHourStr,
                            minute: todoMinuteStr,
                        };
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '每周六':
                        res.type = 'repeatTodo';
                        res.repeatType = 'sat';
                        res.timeDesc = "每周六" + todoHourStr + ":" + todoMinuteStr;
                        res.date = {
                            hour: todoHourStr,
                            minute: todoMinuteStr,
                        };
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    case '每周日':
                        res.type = 'repeatTodo';
                        res.repeatType = 'sun';
                        res.timeDesc = "每周日" + todoHourStr + ":" + todoMinuteStr;
                        res.date = {
                            hour: todoHourStr,
                            minute: todoMinuteStr,
                        };
                        res.timeStr = todoHourStr + ":" + todoMinuteStr;
                        break;
                    default:
                        ;
                }
            } else if (dayTodoRegexMatch && dayTodoRegexMatch.length > 0) {
                const monthValue = parseInt(dayTodoRegexMatch[1]);
                const dateValue = parseInt(dayTodoRegexMatch[2]);
                const hourValue = parseInt(dayTodoRegexMatch[3]);
                let minuteValue = parseInt(dayTodoRegexMatch[4]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                res.date = {
                    month: commonUtil.formatTimeNumber(monthValue),
                    date: commonUtil.formatTimeNumber(dateValue),
                    hour: commonUtil.formatTimeNumber(hourValue),
                    minute: commonUtil.formatTimeNumber(minuteValue),
                };
                const todoDate = new Date(new Date().getFullYear(), monthValue - 1, dateValue, hourValue, minuteValue, 0)
                res.timestamp = todoDate.getTime();
                res.timeDesc = todoDate.toLocaleString();
                res.type = 'onceTodo';
            } else {
                return undefined;
            }
            return res;
        },
        handle: (analysisRes) => {
            // 1. 存储待办数据
            storeUtil.save(storeType.TODO_LIST, analysisRes);
            // 2. 返回反馈数据
            return "好的，我会在" + analysisRes.timeDesc + "提醒你" + analysisRes.title;
        }
    },
    "今天(.*?)点(.*?)": {
        analysis: (userMsg) => {
            let res = {};
            const todayTodoRegexMatch = userMsg.match(/今天(\d{1,2})点(\d{0,2}分?)?(\S+)/);
            let todoHour = todayTodoRegexMatch[1];
            let todoMinute = todayTodoRegexMatch[2];
            if (!todoMinute || todoMinute === '') {
                todoMinute = 0;
            }
            let todo = todayTodoRegexMatch[3];
            res.type = 'onceTodo';
            const nowDate = new Date();
            res.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), todoHour, todoMinute, 0).getTime();
            res.timeDesc = "今天" + new Date(res.timestamp).toLocaleTimeString();
            res.title = todo;
            return res;
        },
        handle: (analysisRes) => {
            // 1. 存储待办数据
            storeUtil.save(storeType.TODO_LIST, analysisRes);
            // 2. 返回反馈数据
            return "好的，我会在" + analysisRes.timeDesc + "提醒你" + analysisRes.title;
        }
    },
    "创建(.*?)的(.*?)事项": {
        analysis: (userMsg) => {
            let res = {};
            if (!userMsg) {
                return undefined;
            }
            // 1.获取时间和标题
            const eventRegex = new RegExp(/^创建(.*?)的(.*)事项$/);
            const eventRegexMatch = userMsg.match(eventRegex);
            const eventTimeOption = eventRegexMatch[1].trim();
            const eventTitle = eventRegexMatch[2].trim();
            res.title = eventTitle;
            // 2.计算时间毫秒值
            const nowDate = new Date();
            let nowDay = nowDate.getDay();
            if (nowDay === 0) {
                nowDay = 7;
            }
            const yearDayTimerRegex = new RegExp(/^(\d{1,4})年(\d{1,2})月(\d{1,2})日(\d{1,2})点(\d{0,2})/);
            const nowYearDayTimerRegex = new RegExp(/^(\d{1,2})月(\d{1,2})日(\d{1,2})点(\d{0,2})/);
            const repeatDayTimerRegex = new RegExp(/^(每天|每周工作日|每周末)(\d{1,2})点(\d{0,2})/);
            const repeatYearTimerRegex = new RegExp(/^每年(\d{1,2})月(\d{1,2})日(\d{1,2})点(\d{0,2})/);
            const yearDayTimerRegexMatch = eventTimeOption.match(yearDayTimerRegex);
            const nowYearDayTimerRegexMatch = eventTimeOption.match(nowYearDayTimerRegex);
            const repeatDayTimerRegexMatch = eventTimeOption.match(repeatDayTimerRegex);
            const repeatYearTimerRegexMatch = eventTimeOption.match(repeatYearTimerRegex);
            if (yearDayTimerRegexMatch && yearDayTimerRegexMatch.length > 0) {
                const yearValue = parseInt(yearDayTimerRegexMatch[1]);
                const monthValue = parseInt(yearDayTimerRegexMatch[2]);
                const dateValue = parseInt(yearDayTimerRegexMatch[3]);
                let hourValue = parseInt(yearDayTimerRegexMatch[4]);
                let minuteValue = parseInt(yearDayTimerRegexMatch[5]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                const todoDate = new Date(yearValue, monthValue - 1, dateValue, hourValue, minuteValue, 0)
                res.timestamp = todoDate.getTime();
                res.type = 'day';
                res.timeDesc = todoDate.toLocaleString();
            } else if (nowYearDayTimerRegexMatch && nowYearDayTimerRegexMatch.length > 0) {
                const monthValue = parseInt(nowYearDayTimerRegexMatch[1]);
                const dateValue = parseInt(nowYearDayTimerRegexMatch[2]);
                let hourValue = parseInt(nowYearDayTimerRegexMatch[3]);
                let minuteValue = parseInt(nowYearDayTimerRegexMatch[4]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                const todoDate = new Date(new Date().getFullYear(), monthValue - 1, dateValue, hourValue, minuteValue, 0)
                res.timestamp = todoDate.getTime();
                res.type = 'fixed-day';
                res.timeDesc = todoDate.toLocaleString();
            } else if (repeatDayTimerRegexMatch && repeatDayTimerRegexMatch.length > 0) {
                const dayType = repeatDayTimerRegexMatch[1];
                const hourValue = parseInt(repeatDayTimerRegexMatch[2]);
                let minuteValue = parseInt(repeatDayTimerRegexMatch[3]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                res.date = {
                    hour: commonUtil.formatTimeNumber(hourValue),
                    minute: commonUtil.formatTimeNumber(minuteValue),
                };
                if (dayType === '每天') {
                    res.type = 'every-day';
                    res.timeDesc = "每天" + res.date.hour + ":" + res.date.minute;
                }
                if (dayType === '每周工作日') {
                    res.type = 'work-day';
                    res.timeDesc = "每周工作日" + res.date.hour + ":" + res.date.minute;
                }
            } else if (repeatYearTimerRegexMatch && repeatYearTimerRegexMatch.length > 0) {
                const monthValue = parseInt(repeatYearTimerRegexMatch[1]);
                const dateValue = parseInt(repeatYearTimerRegexMatch[2]);
                const hourValue = parseInt(repeatYearTimerRegexMatch[3]);
                let minuteValue = parseInt(repeatYearTimerRegexMatch[4]);
                if (!minuteValue || minuteValue === '') {
                    minuteValue = 0;
                }
                res.date = {
                    month: commonUtil.formatTimeNumber(monthValue),
                    date: commonUtil.formatTimeNumber(dateValue),
                    hour: commonUtil.formatTimeNumber(hourValue),
                    minute: commonUtil.formatTimeNumber(minuteValue),
                };
                res.type = 'every-year';
                res.timeDesc = "每年" + res.date.month + "月" + res.date.date + "日" + res.date.hour + ":" + res.date.minute;
            } else {
                return undefined;
            }
            return res;
        },
        handle: (analysisRes) => {
            // 1. 存储事件数据
            storeUtil.save(storeType.EVENT_LIST, analysisRes);
            // 2. 返回反馈数据
            return "好的，已创建" + analysisRes.timeDesc + "的" + analysisRes.title + "事项";
        }
    },
    "设置(.*)为(.*)": {
        analysis: (userMsg) => {
            const setRegExp = new RegExp("^设置(.*)为(.*)$");
            const setRegExpMatch = userMsg.match(setRegExp);
            let analysisRes = {}
            analysisRes.setTypeName = setRegExpMatch[1];
            analysisRes.setTypeValue = setRegExpMatch[2];
            switch (analysisRes.setTypeName) {
                case '大模型接口密钥':
                    analysisRes.setType = storeType.GPT_API_KEY;
                    break;
                case '待办数据':
                    analysisRes.setType = storeType.TODO_LIST;
                    break;
                default:
                    ;
            }
            return analysisRes;
        },
        handle: (analysisRes) => {
            if (!analysisRes.setType) {
                return "暂不支持设置" + analysisRes.setTypeName;
            }
            if (analysisRes.setTypeValue && analysisRes.setTypeValue.trim() !== '空') {
                localStorage.setItem(analysisRes.setType, analysisRes.setTypeValue);
                if (analysisRes.reload) {
                    setTimeout(() => {
                        window.location.reload();
                    }, 1000);
                }
                return "好的，已设置" + analysisRes.setTypeName + "为" + analysisRes.setTypeValue + (analysisRes.reload ? ",页面即将刷新" : "");
            } else {
                return "好的，已取消设置" + analysisRes.setTypeName + (analysisRes.reload);
            }
        },
    },
    "清空(.*)": {
        analysis: (userMsg) => {
            const setRegExp = new RegExp("^清空(.*)$");
            const setRegExpMatch = userMsg.match(setRegExp);
            let analysisRes = {}
            analysisRes.setTypeName = setRegExpMatch[1];
            switch (analysisRes.setTypeName) {
                case '待办数据':
                    analysisRes.setType = storeType.TODO_LIST;
                    break;
                case '待办打卡数据':
                    analysisRes.setType = storeType.TODO_OK;
                    break;
                case '聊天记录':
                    analysisRes.setType = storeType.CHAT_LIST;
                    break;
                case '大模型接口密钥':
                    analysisRes.setType = storeType.GPT_API_KEY;
                    break;
                default:
                    ;
            }
            return analysisRes;
        },
        handle: (analysisRes) => {
            if (!analysisRes.setType) {
                return "暂不支持清空" + analysisRes.setTypeName + "，请手动删除";
            }
            if (analysisRes) {
                if (analysisRes.setType === storeType.TODO_OK) {
                    let toDoList = storeUtil.getDataList(storeType.TODO_LIST);
                    if (toDoList && toDoList.length > 0) {
                        toDoList.forEach(item => {
                            item.okDates = [];
                        });
                        storeUtil.saveAll(storeType.TODO_LIST, toDoList);
                    }
                    storeUtil.delItem(storeType.HABIT_OK_DAYS);
                } else {
                    storeUtil.delAll(analysisRes.setType);
                }
                if (analysisRes.reload) {
                    setTimeout(() => {
                        window.location.reload();
                    }, 1000);
                }
                return "好的，已清空" + analysisRes.setTypeName + (analysisRes.reload ? ",页面即将刷新" : "");
            }
        },
    },
    "导出所有数据": {
        analysis: (userMsg) => {
            let analysisRes = {}
            analysisRes.typeList = [storeType.TODO_LIST];
            return analysisRes;
        },
        handle: (analysisRes) => {
            let appDataJsonObj = {};
            let appDataJsonFileName = "XAssistantData_" + new Date().getTime();
            analysisRes.typeList.forEach(type => {
                appDataJsonObj[type] = storeUtil.getDataList(type);
            });
            if (analysisRes) {
                commonUtil.downloadJsonFile(appDataJsonObj, appDataJsonFileName);
            }
            return "好的，已下载备份数据，" + appDataJsonFileName;
        },
    },
    "(.*)收入|支出(.*)": {
        analysis: (userMsg) => {
            const walletRegex = new RegExp(/^(.*)(收入|支出)(\d{1,10})/);
            const walletRegexMatch = userMsg.match(walletRegex);
            if (!walletRegexMatch || walletRegexMatch.length !== 4 || !walletRegexMatch[1] || !walletRegexMatch[2] || !walletRegexMatch[3]) {
                return undefined;
            }
            let analysisRes = {}
            analysisRes.title = walletRegexMatch[1];
            analysisRes.type = walletRegexMatch[2] === '收入' ? 'in' : 'out';
            analysisRes.money = walletRegexMatch[3];
            return analysisRes;
        },
        handle: (analysisRes) => {
            storeUtil.save(storeType.WALLET_RECORD, analysisRes);
            return "好的，已加入钱包记录";
        },
    },
    "设置月预算为(.*)": {
        analysis: (userMsg) => {
            const monthMaybeOutRegex = new RegExp(/^设置月预算为(\d{1,10})/);
            const monthMaybeOutRegexMatch = userMsg.match(monthMaybeOutRegex);
            if (!monthMaybeOutRegexMatch || monthMaybeOutRegexMatch.length !== 2 || !monthMaybeOutRegexMatch[1]) {
                return undefined;
            }
            let analysisRes = {}
            analysisRes.value = monthMaybeOutRegexMatch[1];
            return analysisRes;
        },
        handle: (analysisRes) => {
            storeUtil.saveItem(storeType.WALLET_MONTH_MAYBE_OUT, analysisRes.value);
            return "好的，设置月预算为" + analysisRes.value;
        },
    },
}
export const askHandleUtil = {
    localHandle(text) {
        // 1.检测关键字
        const textHandleStrategyPatterns = Object.keys(localAskHandleStrategy);
        // 2.获取关键字的策略配置
        let textHandleStrategy = undefined;
        textHandleStrategyPatterns.forEach(pattern => {
            const textMatchRes = text.match(pattern);
            if (textMatchRes && textMatchRes.length > 0) {
                textHandleStrategy = localAskHandleStrategy[pattern];
            }
        });
        if (!textHandleStrategy) {
            return '';
        }
        // 3.执行分析策略，获取分析结果
        const analysisRes = textHandleStrategy.analysis(text);
        if (!analysisRes) {
            return '';
        }
        // 4.执行处理策略
        const calculateRes = textHandleStrategy.handle(analysisRes);
        if (!calculateRes) {
            return '';
        }
        return calculateRes;
    },
    async getAliYunRes(message) {
        let aiApi = 'https://dashscope.aliyuncs.com/compatible-mode/v1/chat/completions';
        let aiApiKey = localStorage.getItem(storeType.GPT_API_KEY);
        let aiApiHeader = {
            "Authorization": aiApiKey,
            "Content-Type": "application/json",
        };
        let chatList = storeUtil.getDataList(storeType.CHAT_LIST);
        let messages = [{
            "role": "user",
            "content": message,
        }];
        if (chatList && chatList.length > 0) {
            messages = [
                {
                    "role": "user",
                    "content": chatList[0].content,
                },
                {
                    "role": "assistant",
                    "content": chatList[1].content,
                },
                {
                    "role": "user",
                    "content": message,
                }
            ]
        }
        let aiApiReqBody = {
            "model": "qwen-plus",
            "messages": messages,
            "stream": true,
            "enable_search": true
        };
        if (!aiApiKey || !aiApi || !aiApiHeader || !aiApiReqBody) {
            return undefined;
        }
        return fetch(aiApi, {
            method: 'POST',
            headers: aiApiHeader,
            body: JSON.stringify(aiApiReqBody)
        }).then(response => response).catch(error => {
            console.log(error);
        });
    },
}