import {useEffect, useState} from "react";
import {commonUtil} from "../assets/js/common/commonUtil";
import {i18nUtil} from "../assets/js/common/i18nUtil";

function TimeCountDown(props) {
    const [leftTimeStr, setLeftTimeStr] = useState('');
    const [big, setBig] = useState(false);
    useEffect(function () {
        setLeftTimeStr(getLeftTimeStr(props.timestamp));
        setInterval(function () {
            setLeftTimeStr(getLeftTimeStr(props.timestamp));
        }, 1000);
        // eslint-disable-next-line
    }, []);

    function getLeftTimeStr() {
        return commonUtil.getLeftTimeStr(props.timestamp) === 0 ? i18nUtil.getText('timeEnd') : i18nUtil.getText('left') + " " + commonUtil.getLeftTimeStr(props.timestamp);
    }

    return (
        <div className="timeCount" onClick={() => {
            setBig(big);
        }}>
            {big ? (props.title + leftTimeStr) : leftTimeStr}
        </div>
    )
}

export default TimeCountDown;