import {themeUtil} from "./assets/js/common/themeUtil";
import "./assets/css/common/theme.css";
import "./assets/css/common/common.css";
import Assistant from "./page/Assistant";
import {useEffect} from "react";
import {i18nUtil} from "./assets/js/common/i18nUtil";
import {notificationUtil} from "./assets/js/common/notification";

function App() {
    useEffect(function () {
        notificationUtil.allowNotification();
        themeUtil.autoTheme();
        document.title = i18nUtil.getText("assistant");
    }, []);
    return (
        <div>
            <Assistant/>
        </div>
    );
}

export default App;
