import {i18nUtil} from "../../../assets/js/common/i18nUtil";
import {commonUtil} from "../../../assets/js/common/commonUtil";
import {storeType, storeUtil} from "../../../assets/js/common/storeUtil";
import {Delete} from "@icon-park/react";

function WalletFeature(props) {
    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className='feature-head-left-title'>{i18nUtil.getText("Wallet")}</div>
                    </div>
                    <div className="feature-head-right">

                    </div>
                </div>
                <div className="feature-content">
                    <div className="wallet">
                        <div className="wallet-left">
                            <div className="wallet-out">
                                <div className="wallet-out-title">{new Date().getMonth() + 1}月消费</div>
                                <div className="wallet-out-info">
                                    <span className="out-money">{props.monthOutMoney}</span>
                                    <span>/</span>
                                    <span className='month-maybe-money'>{props.monthMaybeOut}</span>
                                </div>
                            </div>
                            <div className="wallet-store">
                                <div className="month-maybe-store">
                                    <div>月预存款</div>
                                    <div className="money-color">{props.monthMaybeStore}</div>
                                </div>
                                <div className="year-maybe-store">
                                    <div>年预存款</div>
                                    <div className="money-color">{props.yearMaybeStore}</div>
                                </div>
                            </div>
                        </div>
                        <div className="wallet-right">
                            <div className="wallet-record">
                                <div className='sys-empty'
                                     style={{
                                         display: props.walletRecord.length === 0 ? undefined : "none",
                                         height: '100%'
                                     }}>无收支记录
                                </div>
                                {
                                    props.walletRecord.map((item, index) => {
                                        return (
                                            <div className={`wallet-record-item ${item.type === "out" ? "out" : "in"}`}
                                                 key={index}>
                                                <div
                                                    className="wallet-record-item-money">{item.type === "out" ? "-" : "+"}{item.money}</div>
                                                <div className="wallet-record-item-title">{item.title}</div>
                                                <div
                                                    className="wallet-record-item-time">{new Date(item.createTime).toLocaleDateString()}</div>
                                                <div onClick={async () => {
                                                    if (await commonUtil.confirm('确定删除' + item.title + (item.type === "out" ? "-" : "+") + item.money + "?")) {
                                                        storeUtil.delById(storeType.WALLET_RECORD, item.dataId);
                                                        props.loadWalletData();
                                                    }
                                                }}>
                                                    <Delete fill={'var(--danger-color)'}/>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <div className="wallet-info">
                                <div className="total-out">
                                    <div>总消费</div>
                                    <div className="out-money-color">{props.totalOutMoney}</div>
                                </div>
                                <div className="total-money">
                                    <div>总资金</div>
                                    <div className="money-color">{props.totalMoney}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default WalletFeature;