export const i18nUtil = {
    getText(code) {
        let res = '';
        switch (lang) {
            case 'zh':
                res = cnTextMap[code];
                break;
            case 'en':
                res = enTextMap[code];
                break;
            default:
                res = cnTextMap[code];
        }
        return res;
    }
}