import {i18nUtil} from "../../../assets/js/common/i18nUtil";
import {storeType, storeUtil} from "../../../assets/js/common/storeUtil";
import TimeCountDown from "../../../component/TimeCountDown";
import {Delete} from "@icon-park/react";
import {commonUtil} from "../../../assets/js/common/commonUtil";

function EventFeature(props) {
    return (
        <div className="feature-item-row">
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="feature-head-left-title">{i18nUtil.getText('event')}</div>
                    </div>
                    <div className="feature-head-right">

                    </div>
                </div>
                <div className="feature-content">
                    <div className="event-item-list">
                        {
                            props.eventList.map((item, index) => (
                                <div className="operate-row" key={item.id + index}>
                                    <div className="event-item">
                                        <div className="event-item-head">
                                            <div className="event-type">{item.titleIcon}</div>
                                            <div className="event-time-info">
                                                <div className="event-date">{item.date}</div>
                                                <div className="event-time">{item.time}</div>
                                            </div>
                                        </div>
                                        <div className="event-item-content">
                                            <div className="event-title">{item.title}</div>
                                            <div className="event-left-info">
                                                <div className="event-left-day">
                                                    <TimeCountDown timestamp={item.timestamp} title={item.title}/>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div style={{display: item.type === "holiday" ? 'none' : undefined}}
                                         className="operate-row-btn">
                                        <Delete theme="filled" fill='var(--danger-color)' className="del-btn"
                                                onClick={async () => {
                                                    if (await commonUtil.confirm('确定删除' + item.title + i18nUtil.getText('event'))) {
                                                        storeUtil.delById(storeType.EVENT_LIST, item.id);
                                                        props.loadEventList();
                                                    }
                                                }}/>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                    <div style={{display: props.eventList.length === 0 ? undefined : 'none'}}
                         className="sys-empty">{i18nUtil.getText('emptyHit')}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default EventFeature;