import '../assets/css/page/assistant.css'
import AskInput from './chat/AskInput';
import {storeType, storeUtil} from '../assets/js/common/storeUtil';
import {useEffect, useState} from 'react';
import {commonUtil} from '../assets/js/common/commonUtil';
import {i18nUtil} from '../assets/js/common/i18nUtil';
import FeatureList from './feature/FeatureList';
import MarkdownViewModal from "../component/MarkdownViewModal";
import AnswerModal from "./chat/AnswerModal";
import NotificationModal from "../component/NotificationModal";
import {askHandleUtil} from "../assets/js/page/chat";
import {notificationUtil} from "../assets/js/common/notification";
import assistantLogo from "../assets/img/assistant-logo.png";

function Assistant() {
    const [chatRecords, setChatRecords] = useState([]);
    const [showChatRecords, setShowChatRecords] = useState(false);
    const [habitList, setHabitList] = useState([]);
    const [todayTodoList, setTodayTodoList] = useState([]);
    const [timerList, setTimerList] = useState([]);
    const [eventList, setFocusThingList] = useState([]);
    const [noteList, setNoteList] = useState([]);
    const [ask, setAsk] = useState('');
    const [answer, setAnswer] = useState('');
    const [answerType, setAnswerType] = useState('text');
    const [headNotification, setHeadNotification] = useState('');
    const [mainNotificationList, setMainNotificationList] = useState([]);
    const [showEditNote, setShowEditNote] = useState(false);
    const [locationInfo, setLocationInfo] = useState({city: '', weather: ''});
    const [dangerEventList, setDangerEventList] = useState([]);
    const [netStreamStart, setNetStreamStart] = useState(false);
    const [walletRecord, setWalletRecord] = useState([]);
    const [monthOutMoney, setMonthOutMoney] = useState(0);
    const [monthMaybeOut, setMonthMaybeOut] = useState(0);
    const [monthMaybeStore, setMonthMaybeStore] = useState(0);
    const [yearMaybeStore, setYearMaybeStore] = useState(0);
    const [totalOutMoney, setTotalOutMoney] = useState(0);
    const [totalMoney, setTotalMoney] = useState(0);

    // ask
    async function handleSendText(sendText) {
        if (!sendText || sendText === '') {
            return;
        }
        if (sendText === '记笔记') {
            setShowEditNote(true);
            return;
        }
        setAsk(sendText);
        setAnswerType('text');
        setAnswer(i18nUtil.getText('thinking') + '...');
        // 本地处理用户问题
        let localThinkRes = askHandleUtil.localHandle(sendText);
        const userMsgId = storeUtil.save(storeType.CHAT_LIST, {from: 'user', content: sendText});
        if (localThinkRes && localThinkRes !== '') {
            setAnswerType('text');
            setAnswer(localThinkRes);
            // 存储对话
            storeUtil.save(storeType.CHAT_LIST, {
                from: 'assistant',
                userMsgId: userMsgId,
                content: localThinkRes,
                type: 'text'
            });
            // 重新加载数据
            reloadData();
        } else {
            // 云处理用户消息
            let loadingChatData = {from: 'assistant'};
            let netThinkResponse = await askHandleUtil.getAliYunRes(sendText);
            if (netThinkResponse) {
                setNetStreamStart(true);
                setAnswerType('md');
                let reader = netThinkResponse.body.getReader();
                const decoder = new TextDecoder('utf-8');
                let done = false;
                setAnswer('');
                let answerText = '';
                while (!done) {
                    const {value, done: readerDone} = await reader.read();
                    done = readerDone;
                    const chunkJson = decoder.decode(value, {stream: true}).split("data:")[1];
                    if (!chunkJson || chunkJson.trim() === '[DONE]') {
                        loadingChatData.content = answerText;
                        loadingChatData.type = 'md';
                        loadingChatData.userMsgId = userMsgId;
                        // 存储对话
                        storeUtil.save(storeType.CHAT_LIST, loadingChatData);
                        loadChatRecords();
                        setNetStreamStart(false);
                    } else {
                        answerText += JSON.parse(chunkJson).choices[0].delta.content;
                        setAnswer(answerText);
                        setTimeout(function () {
                            commonUtil.scrollToBottom('assistant-answer-content');
                        }, 100);
                    }
                }
            } else {
                setAnswer(i18nUtil.getText('cantTalkText'));
                setAnswerType('text');
                loadChatRecords();
                loadingChatData.content = answer;
                loadingChatData.type = 'text';
                loadingChatData.userMsgId = userMsgId;
                // 存储对话
                storeUtil.save(storeType.CHAT_LIST, loadingChatData);
            }
        }
    }

    function showAskRecord(record) {
        let chatList = storeUtil.getDataList(storeType.CHAT_LIST);
        let chatAnswer = chatList.filter(chatItem => chatItem.userMsgId === record.dataId)[0];
        if (!chatAnswer) {
            chatAnswer = {content: i18nUtil.getText('thinking') + "..."};
        }
        setTimeout(function () {
            commonUtil.scrollToTop('assistant-answer-content');
        }, 100);
        setAsk(record.content);
        setAnswer(chatAnswer.content);
        setAnswerType(chatAnswer.type);
    }

    // feature
    function loadHabitList() {
        let todoList = storeUtil.getDataList(storeType.TODO_LIST);
        todoList = todoList.filter(todo => {
            if (todo.type === 'repeatTodo') {
                let nowDay = new Date().getDay();
                switch (todo.repeatType) {
                    case 'everyday':
                        return true;
                    case 'workday':
                        if (nowDay !== 0 && nowDay !== 6) {
                            return true;
                        }
                        return false;
                    case 'restday':
                        if (nowDay === 0 || nowDay === 6) {
                            return true;
                        }
                        return false;
                    case 'sat':
                        if (nowDay === 6) {
                            return true;
                        }
                        return false;
                    case 'sun':
                        if (nowDay === 0) {
                            return true;
                        }
                        return false;
                    default:
                        return false;
                }
            }
            return false;
        });
        todoList = commonUtil.sortArray(todoList, 'timeStr');
        setHabitList(todoList);
    }

    function loadTodayTodoList() {
        let todoList = storeUtil.getDataList(storeType.TODO_LIST);
        let nowDate = new Date().getDate();
        todoList = todoList.filter(todo => (todo.type === 'onceTodo' && new Date(todo.timestamp).getDate() === nowDate));
        todoList = commonUtil.sortArray(todoList, 'timestamp');
        setTodayTodoList(todoList);
    }

    function loadTimerList() {
        let todoList = storeUtil.getDataList(storeType.TODO_LIST);
        todoList = todoList.filter(todo => (todo.type === 'timer'));
        todoList = commonUtil.sortArray(todoList, 'timestamp');
        setTimerList(todoList);
    }

    function loadEventList() {
        let eventDataList = storeUtil.getDataList(storeType.EVENT_LIST);
        const holidayDateMap = commonUtil.getHolidayDate();
        let eventListTemp = [];
        setFocusThingList([]);
        Object.keys(holidayDateMap).forEach(title => {
            eventDataList.push({
                dataId: 'holiday' + new Date().getTime(),
                title: title,
                type: 'holiday',
                timestamp: holidayDateMap[title],
            });
        });
        eventDataList.forEach(event => {
            let titleIcon = '🪧';
            if (event.title.indexOf('新年') !== -1 || event.title.indexOf('跨年') !== -1) {
                titleIcon = '🎉'
            }
            if (event.title.indexOf('圣诞') !== -1) {
                titleIcon = '🎄'
            }
            if (event.title.indexOf('春节') !== -1) {
                titleIcon = '🧧'
            }
            if (event.title.indexOf('劳动节') !== -1) {
                titleIcon = '🛠️'
            }
            if (event.title.indexOf('中秋节') !== -1) {
                titleIcon = '🎑'
            }
            if (event.title.indexOf('国庆节') !== -1) {
                titleIcon = '🇨🇳'
            }
            if (event.title.indexOf('地铁') !== -1) {
                titleIcon = '🚇'
            }
            if (event.type === 'every-year') {
                let year = new Date().getFullYear();
                if (parseInt(event.date.month) === 1 && parseInt(event.date.date) === 1
                    && parseInt(event.date.hour) === 0 && parseInt(event.date.minute) === 0) {
                    year += 1;
                }
                event.timestamp = new Date(year, event.date.month - 1, event.date.date, event.date.hour, event.date.minute).getTime();
            }
            if (event.type === 'every-day') {
                let now = new Date();
                event.timestamp = new Date(now.getFullYear(), now.getMonth(), now.getDate(), event.date.hour, event.date.minute).getTime();
            }
            if (event.type === 'work-day') {
                let now = new Date();
                event.timestamp = new Date(now.getFullYear(), now.getMonth(), now.getDate(), event.date.hour, event.date.minute).getTime();
            }
            let somethingDate = new Date(event.timestamp);
            let leftMils = somethingDate.getTime() - new Date().getTime();
            let add = true;
            switch (event.type) {
                case 'every-year':
                case 'holiday':
                    if (leftMils > 90 * 24 * 60 * 60 * 1000 || leftMils < -24 * 60 * 60 * 1000) {
                        add = false;
                    }
                    break;
                case 'fixed-day':
                    if (leftMils > 30 * 24 * 60 * 60 * 1000 || leftMils < -24 * 60 * 1000) {
                        add = false;
                    }
                    break;
                case 'work-day':
                    if (new Date().getDay() === 0 || new Date().getDay() === 6 || leftMils > 30 * 60 * 1000 || leftMils < -5 * 60 * 1000) {
                        add = false;
                    }
                    break;
                case 'every-day':
                    if (leftMils > 30 * 60 * 1000 || leftMils < 0) {
                        add = false;
                    }
                    break;
                default:
                    ;
            }
            if (add) {
                eventListTemp.push({
                    id: event.dataId,
                    title: event.title,
                    titleIcon: titleIcon,
                    date: somethingDate.toLocaleDateString(),
                    time: somethingDate.toLocaleTimeString(),
                    timestamp: event.timestamp,
                    type: event.type
                });
            }
        });
        eventListTemp = commonUtil.sortArray(eventListTemp, 'timestamp');
        setFocusThingList(eventListTemp);
    }

    function loadChatRecords() {
        let chatList = storeUtil.getDataList(storeType.CHAT_LIST);
        let chatRecords = chatList.filter(chatItem => chatItem.from === 'user');
        chatRecords = commonUtil.sortArray(chatRecords, "createTime", false);
        setChatRecords(chatRecords);
        setTimeout(() => {
            commonUtil.scrollToLeft("chat-record-list");
        }, 100);
    }

    function loadNoteList() {
        let noteList = storeUtil.getDataList(storeType.NOTE_LIST);
        setNoteList(noteList);
    }

    async function loadLocationInfo() {
        let city = await commonUtil.getLocationInfo();
        let weather = await commonUtil.getWeatherInfo();
        setLocationInfo({city: city, weather: weather});
    }

    async function loadDangerEventList() {
        setDangerEventList(await commonUtil.getDangerEvents());
    }

    function loadWalletData() {
        // walletRecords
        let walletRecord = storeUtil.getItem(storeType.WALLET_RECORD);
        if (walletRecord) {
            setWalletRecord(walletRecord);
        }
        // monthMaybeOut
        let monthMaybeOut = storeUtil.getItem(storeType.WALLET_MONTH_MAYBE_OUT);
        if (monthMaybeOut) {
            setMonthMaybeOut(monthMaybeOut);
        }
        if (!walletRecord || walletRecord.length === 0) {
            setMonthOutMoney(0);
            setMonthMaybeStore(0);
            setYearMaybeStore(0);
            setTotalOutMoney(0);
            setTotalMoney(0);
            return;
        }
        let monthOutMoney = 0;
        let monthMaybeStore = 0;
        let yearMaybeStore = 0;
        let totalOutMoney = 0;
        let totalMoney = 0;
        walletRecord.forEach(item => {
            if (item.type === "out") {
                if (new Date().getMonth() === new Date(item.createTime).getMonth()) {
                    monthOutMoney += parseInt(item.money);
                    monthMaybeStore -= parseInt(item.money);
                } else {
                    yearMaybeStore -= parseInt(item.money);
                }
                totalOutMoney += parseInt(item.money);
                totalMoney -= parseInt(item.money);
            }
            if (item.type === "in") {
                if (new Date().getMonth() === new Date(item.createTime).getMonth()) {
                    monthMaybeStore += parseInt(item.money);
                } else {
                    yearMaybeStore += parseInt(item.money);
                }
                totalMoney += parseInt(item.money);
            }
        });
        yearMaybeStore += monthMaybeStore;

        setMonthOutMoney(monthOutMoney);
        setMonthMaybeStore(monthMaybeStore);
        setYearMaybeStore(yearMaybeStore);
        setTotalOutMoney(totalOutMoney);
        setTotalMoney(totalMoney);
    }

    function reloadData() {
        loadChatRecords();
        loadNotification();
        loadHabitList();
        loadTodayTodoList();
        loadTimerList();
        loadEventList();
        loadWalletData();
    }

    function reloadToDoData() {
        loadHabitList();
        loadTodayTodoList();
        loadTimerList();
    }

    // notification
    function loadNotification() {
        const notificationDataTypes = [storeType.TODO_LIST];
        // 初始化通知定时器
        for (const dataType of notificationDataTypes) {
            const dataList = storeUtil.getDataList(dataType);
            for (const data of dataList) {
                // 跳过已完成
                if (data.ok) {
                    continue;
                }
                if (data.okDates && data.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
                    continue;
                }
                // 重复的计算时间点
                if (data.type === 'repeatTodo') {
                    const nowDate = new Date();
                    switch (data.repeatType) {
                        case 'everyday':
                            data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), data.date.hour, data.date.minute, 0).getTime();
                            break;
                        case 'workday':
                            if (nowDate.getDay() !== 0 && nowDate.getDay() !== 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), data.date.hour, data.date.minute, 0).getTime();
                            }
                            break;
                        case 'restday':
                            if (nowDate.getDay() === 0 || nowDate.getDay() === 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), data.date.hour, data.date.minute, 0).getTime();
                            }
                            break;
                        case 'sat':
                            if (nowDate.getDay() === 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), data.date.hour, data.date.minute, 0).getTime();
                            }
                            break;
                        case 'sun':
                            if (nowDate.getDay() === 0) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), data.date.hour, data.date.minute, 0).getTime();
                            }
                            break;
                        default:
                            ;
                    }
                }
                if (!data.timestamp) {
                    continue;
                }
                const endMils = data.timestamp - new Date().getTime();
                if (endMils > 0) {
                    data.isExpired = false;
                    setTimeout(function () {
                        handleNotification(dataType, data);
                    }, endMils);
                } else {
                    data.isExpired = true;
                    handleNotification(dataType, data);
                }
            }
        }
    }

    function handleNotification(dataType, data) {
        // 已删除，不提醒
        let newestData = storeUtil.getById(dataType, data.dataId);
        if (!newestData) {
            return false;
        }
        if (newestData.ok) {
            return false;
        }
        if (newestData.okDates && newestData.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
            return false;
        }
        if (newestData.alertDates && newestData.alertDates.indexOf(new Date().toLocaleDateString()) !== -1) {
            return false;
        }
        // 系统通知
        notificationUtil.notification(new Date(data.timestamp).toLocaleTimeString() + ":" + data.title);
        // 页面通知
        let existArray = mainNotificationList.filter(item => item.data.dataId === data.dataId);
        if (existArray.length === 0) {
            setMainNotificationList(mainNotificationList => [...mainNotificationList, {
                title: data.title + i18nUtil.getText('timeEnd'),
                time: new Date(data.timestamp).toLocaleTimeString(),
                data: newestData,
                dataType: dataType,
            }]);
        }
        return true;
    }

    function initData() {
        loadChatRecords();
        loadNotification();
        loadHabitList();
        loadTodayTodoList();
        loadTimerList();
        loadEventList();
        loadNoteList();
        storeUtil.storeLocation();
        loadLocationInfo().then();
        loadDangerEventList().then();
        loadWalletData();
        if (!localStorage.getItem('firstHello')) {
            setHeadNotification(i18nUtil.getText('helloText'));
            setTimeout(function () {
                setHeadNotification('');
            }, 60 * 1000);
            localStorage.setItem('firstHello', '1');
        } else {
            setHeadNotification(commonUtil.getIndexHelloText());
            setTimeout(function () {
                setHeadNotification('');
            }, 5 * 1000);
        }
    }

    useEffect(function () {
        initData();
        const handleVisibilityChange = () => {
            if (document.visibilityState === 'visible') {
                setMainNotificationList([]);
                initData();
            }
        };
        document.addEventListener('visibilitychange', handleVisibilityChange);

        return () => {
            document.removeEventListener('visibilitychange', handleVisibilityChange);
        };
        // eslint-disable-next-line
    }, []);
    return (
        <div className="assistant">
            <div className="assistant-head">
                <div className="assistant-head-logo">
                    <img src={assistantLogo} alt=""/>
                </div>
                <div className="assistant-head-notification"
                     style={{display: headNotification === '' ? 'none' : undefined}}>
                    <div className="assistant-head-notification-item animate-scale-in">{headNotification}</div>
                </div>
            </div>
            <div className='assistant-main'>
                <AnswerModal close={() => {
                    setAsk('');
                    setAnswer('');
                    setNetStreamStart(false);
                }} content={{ask: ask, answer: answer, answerType: answerType}}/>
                <MarkdownViewModal show={showEditNote} hide={() => setShowEditNote(false)} edit={true} note={''}
                                   loadNoteList={() => loadNoteList()}/>
                <NotificationModal mainNotificationList={mainNotificationList}
                                   setMainNotificationList={(value) => setMainNotificationList(value)}
                                   close={() => {
                                       if (mainNotificationList.length === 0) {
                                           setAsk('');
                                           setAnswer('');
                                       }
                                   }}
                                   reloadToDoData={() => reloadToDoData()}/>
                <FeatureList show={true}
                             dangerEventList={dangerEventList} loadDangerEventList={() => loadDangerEventList()}
                             chatRecords={chatRecords} loadChatRecords={() => loadChatRecords()}
                             habitList={habitList} setHabitList={(value) => setHabitList(value)}
                             loadHabitList={() => loadHabitList()}
                             todayTodoList={todayTodoList} setTodayTodoList={(value) => setTodayTodoList(value)}
                             loadTodayTodoList={() => loadTodayTodoList()}
                             timerList={timerList} setTimerList={(value) => setTimerList(value)}
                             loadTimerList={() => loadTimerList()}
                             eventList={eventList}
                             loadEventList={() => loadEventList()}
                             noteList={noteList}
                             loadNoteList={() => loadNoteList()}
                             locationInfo={locationInfo}
                             monthMaybeOut={monthMaybeOut}
                             monthOutMoney={monthOutMoney}
                             monthMaybeStore={monthMaybeStore}
                             yearMaybeStore={yearMaybeStore}
                             totalOutMoney={totalOutMoney}
                             totalMoney={totalMoney}
                             walletRecord={walletRecord}
                             loadWalletData={() => loadWalletData()}
                             showChatRecord={(record) => {
                                 showChatRecords(record);
                             }}/>
            </div>
            <div className='assistant-bottom'>
                <AskInput
                    show={!netStreamStart}
                    chatRecords={chatRecords}
                    loadChatRecords={() => loadChatRecords()}
                    showChatTalk={(record) => showAskRecord(record)}
                    showChatRecords={showChatRecords}
                    setShowChatRecords={(value) => setShowChatRecords(value)}
                    handleSendText={(sendText) => {
                        handleSendText(sendText).then();
                    }}/>
            </div>
        </div>
    );
}

export default Assistant;