import {i18nUtil} from "../../../assets/js/common/i18nUtil";
import {useState} from "react";
import MarkdownViewModal from "../../../component/MarkdownViewModal";
import {Delete} from "@icon-park/react";
import {storeType, storeUtil} from "../../../assets/js/common/storeUtil";
import {commonUtil} from "../../../assets/js/common/commonUtil";

function NoteFeature(props) {
    const [note, setNote] = useState('');

    function showNote(note) {
        setNote(note);
    }

    return (
        <div className="feature-item-row">
            <MarkdownViewModal show={note !== ''} edit={false} note={note} hide={() => {
                setNote('')
            }}/>
            <div className="feature-item">
                <div className="feature-head">
                    <div className="feature-head-left">
                        <div className="feature-head-left-title">{i18nUtil.getText('note')}</div>
                    </div>
                    <div className="feature-head-right flex-center">
                    </div>
                </div>
                <div className="feature-content">
                    <div className='note-list'>
                        {
                            props.noteList.map((item, index) => {
                                return (
                                    <div className='operate-row'>
                                        <div className="note-item" key={item.dataId + index}>
                                            <div className='note-info' onClick={() => {
                                                showNote(item);
                                            }}>
                                                <div className='note-title'>{item.title}</div>
                                                <div className='note-time'>{item.time}</div>
                                            </div>
                                        </div>
                                        <div className='operate-row-btn'>
                                            <Delete theme="filled" fill='var(--danger-color)' className="del-btn"
                                                    onClick={async () => {
                                                        if (await commonUtil.confirm('确定删除' + item.title + i18nUtil.getText('note') + '吗')) {
                                                            storeUtil.delById(storeType.NOTE_LIST, item.dataId);
                                                            props.loadNoteList();
                                                        }
                                                    }}/>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <div style={{display: props.noteList.length === 0 ? undefined : 'none'}}
                     className="sys-empty">{i18nUtil.getText('emptyHit')}
                </div>
            </div>
        </div>
    )
}

export default NoteFeature;