import {storeType, storeUtil} from "../assets/js/common/storeUtil";
import {CheckCircleFill, CloseCircleFill} from "antd-mobile-icons";

function NotificationModal(props) {
    return (
        <div>
            <div className="modal-mask"
                 style={{display: props.mainNotificationList.length === 0 ? 'none' : undefined}}
                 onClick={() => props.close()}></div>
            <div style={{display: props.mainNotificationList.length === 0 ? 'none' : undefined}}>
                {
                    props.mainNotificationList.map((item, index) => {
                        return (
                            <div key={index} className="main-notification animate-popDown">
                                <div className="main-notification-info">{item.title}</div>
                                <div className="main-notification-time">{item.time}</div>
                                <div className="main-notification-operate"
                                     style={{display: (item.data.type === 'timer') ? undefined : 'none'}}>
                                    <div className='main-notification-operate-btn' onClick={() => {
                                        props.setMainNotificationList(mainNotificationList =>
                                            mainNotificationList.filter((_, i) => i !== index)
                                        );
                                        if (item.dataType === storeType.TODO_LIST) {
                                            item.data.ok = true;
                                            storeUtil.updateById(storeType.TODO_LIST, item.data);
                                        }
                                        props.reloadToDoData();
                                    }}>
                                        <CloseCircleFill color="var(--danger-color)"/>
                                    </div>
                                </div>
                                <div className="main-notification-operate"
                                     style={{display: (item.data.type === 'repeatTodo' || item.data.type === 'onceTodo') ? undefined : 'none'}}>
                                    <div className='main-notification-operate-btn' onClick={() => {
                                        props.setMainNotificationList(mainNotificationList =>
                                            mainNotificationList.filter((_, i) => i !== index)
                                        );
                                        if (item.data.type === 'repeatTodo') {
                                            item.data.okDates = item.data.okDates ? [...item.data.okDates, new Date().toLocaleDateString()] : [new Date().toLocaleDateString()];
                                        }
                                        if (item.data.type === 'onceTodo') {
                                            item.data.ok = true;
                                        }
                                        storeUtil.updateById(storeType.TODO_LIST, item.data);
                                        props.reloadToDoData();
                                    }}>
                                        <CheckCircleFill color="var(--success-color)"/>
                                    </div>
                                    <div className='main-notification-operate-btn' onClick={() => {
                                        props.setMainNotificationList(mainNotificationList =>
                                            mainNotificationList.filter((_, i) => i !== index)
                                        );
                                        item.data.alertDates = item.data.alertDates ? [...item.data.alertDates, new Date().toLocaleDateString()] : [new Date().toLocaleDateString()];
                                        storeUtil.updateById(storeType.TODO_LIST, item.data);
                                    }}>
                                        <CloseCircleFill color="var(--danger-color)"/>
                                    </div>
                                </div>
                            </div>
                        );
                    })
                }
            </div>
        </div>
    );
}

export default NotificationModal;